import React from "react";
import styled from "styled-components";

const ParralaxContainer = styled.div`
  min-height: 100vh;
  padding: 30vw 0 5vw;
  position: relative;
  transform-style: inherit;
  width: 100vw;
`;

const ParallaxBackLayer = styled.div``;

const ParallaxBaseLayer = styled.div``;

const Parallax = ({ children }) => (
  <ParralaxContainer>
    <ParallaxBaseLayer>{children}</ParallaxBaseLayer>
  </ParralaxContainer>
);
export default Parallax;



